<template>
	<div class="home">
		<h1>Home</h1>
		<h2>Author: {{ userName }}</h2>
		<button @click="incCounter">+1</button>
		<input type="number" v-model="value" />
		<button @click="setCounter(value)">setCounter</button>
		{{ counter }}
		<h3>Marks</h3>
		<input type="range" min="1" max="12" v-model="mark" />{{ mark }}
		<ul>
			<li v-for="(m, index) in getMarks(mark)" :key="index">{{ m }}</li>
		</ul>
	</div>
</template>

<script>
	import { mapState, mapGetters, mapMutations } from 'vuex';

	export default {
		name: 'home',

		data: () => ({
			value: 20,
		}),

		computed: {
			mark: {
				get() {
					return this.$store.state.mark;
				},
				set(value) {
					this.$store.state.mark = value;
				},
			},

			...mapState(['counter', 'marks']),
			...mapState({
				userName: 'name',
			}),
			...mapGetters(['getGoodMarks', 'getMarks']),
		},

		methods: {
			...mapMutations(['incCounter', 'setCounter']),
		},
	};
</script>
